import { createRouter, createWebHashHistory} from 'vue-router'
import login from '@/views/login.vue'
import index from '@/components/index/index.vue'
import dataana from '@/components/dataana/DataAna.vue'
import dataman from '@/components/dataman/DataMan.vue'
import busman from '@/components/dataman/BusMan.vue'
import setting from '@/components/setting/setting.vue'
import addGoods from '@/components/addData/addGoods.vue'
import foodman from '@/components/dataman/FoodMan.vue'
import tubiao from '@/components/tubiao/tubiao.vue'
import store from '@/store'

const routes = [
  { path:'/', redirect:'/login' },
  { path:'/login', component:login, name:'login'},
  { path:'/index', component:index, name:'index', redirect:'/index/dataana' ,children:[
    { path:'dataana', component:dataana, meta:{index:'1',info:'首页'}},
    { path:'dataman', component:dataman, meta:{index:'2-1',info:'用户列表',title:'数据管理'} },
    { path:'busman', component:busman, meta:{index:'2-2',info:'商家列表',title:'数据管理'} },
    { path:'foodman', component:foodman, meta:{index:'2-3',info:'菜品列表',title:'数据管理'} },
    { path:'addGoods', component:addGoods, meta:{index:'3-1',info:'添加商品',title:'添加数据'}},
    { path:'tubiao', component:tubiao, meta:{index:'4-1',info:'用户分布',title:'图表'}},
    { path:'setting', component:setting, meta:{index:'5-1',info:'头像设置',title:'设置'} }
  ]},
]

const routerHistory = createWebHashHistory()
const router = createRouter({
  //history: createWebHistory(process.env.BASE_URL),
  history:routerHistory,
  routes
})

router.beforeEach((to, from, next) => {
  const token = localStorage.getItem('token')
  if(to.path === '/login') {
    if(token) {
      $message({
        showClose: true,
        grouping:true,
        message:'检测到您之前已登录,已为您自动登录',
        type:'success'
      })
      return next('/index')
    }
    next()
  }else{
    if(!token) return next('/login')
    store.commit('setfull',to.meta.title)
    store.commit('setbase',to.meta.info)
    store.commit('setAc',to.meta.index)
    next()
  }
})

export default router
