import axios from 'axios'
import {interceptorApi} from './Interceptor.js'

axios.defaults.baseURL = 'http://127.0.0.1:3007'
axios.defaults.headers = {
    'Content-Type':'application/x-www-form-urlencoded',
}

export const register = function(data){
    let code = new Promise(resolve => {
        axios.request({
            method:'post',
            url:'/api/reguser',
            data:data
        }).then(res => {
            resolve(interceptorApi(res.data))
        })
    })
        return code
}

export const login = function(data){
    let code = new Promise(resolve => {
        axios.request({
            method:'post',
            url:'/api/login',
            data:data
        }).then(res => {
            resolve(interceptorApi(res.data))
        })
    })
        return code
}

export const getuserInfo = function(data){
    const token = localStorage.getItem('token')
    let code = new Promise(resolve => {
        axios.request({
            method:'GET',
            url:'/my/userinfo',
            params:data,
            headers: {
                Authorization: token
              }
        }).then(res => {
            resolve(interceptorApi(res.data))
        })
    })
        return code
}

