//import { ElMessage } from 'element-plus'

export const interceptorApi = async (res) => {
    if(res.status === 1){
        if(res.code){
            localStorage.removeItem('token')
            $message({
                showClose: true,
                message:res.message,
                type:'error'
            })
            $router.push('/login')
        }else{
            $message({
                showClose:true,
                message:res.message,
                type:'error'
            })
        }
        return Promise.reject(res.message)
    }else if(res.status === 0) {
        if(res.code){
            //注册成功
            $message({
                showClose: true,
                message:res.message,
                type:'success'
            })
        }else{
            //登录成功
            const token = res.token
            if(token){
                localStorage.setItem('token', token)
                localStorage.setItem('info', JSON.stringify(res.info))
                $message({
                    showClose: true,
                    message:res.message,
                    type:'success'
                })
            }else{
                if(res.message){
                    $message({
                        showClose: true,
                        message:res.message,
                        type:'success'
                    })
                }
                return res.data
            }
        }
    }
}