<template>
    <router-view v-if="isRouterAlive"></router-view>
</template>

<script>
import * as echarts from 'echarts'
export default {
    provide(){
        return{
            reload:this.reload,
            'echarts':echarts
        }
    },
    data(){
        return{
            isRouterAlive:true
        }
    },
    methods:{
        reload(){
            this.isRouterAlive = false
            this.$nextTick(function(){
                this.isRouterAlive = true
            })
        }
    }
}
</script>

