import { createStore } from 'vuex'
import createPersistedState from "vuex-persistedstate";

export default createStore({
  state: {
    defaultactive:'1',
    fullname:'',
    basename:'',
  },
  getters: {
  },
  mutations: {
    setAc(state,index){
      state.defaultactive = index
    },
    setfull(state,name){
      state.fullname = name
    },
    setbase(state,name){
      state.basename = name
    },
  },
  actions: {
  },
  modules: {
  },
  plugins: [createPersistedState({ storage: window.sessionStorage })]
})
