<template>
  <el-table :data="tableData" height="500" style="width: 100%" :header-cell-style="{ 
      background:'#eef1f6',color:'#000000',fontWeight:600}" :cell-style="{color:'#000'}">
    <el-table-column label="#" type="index" min-width="50"/>
    <el-table-column label="注册时间" min-width="100">
      <template #default="scope">
        <div style="display: flex; align-items: center">
          <el-icon><timer /></el-icon>
          <span style="margin-left: 10px">{{ scope.row.date }}</span>
        </div>
      </template>
    </el-table-column>
    <el-table-column label="用户姓名" min-width="100">
      <template #default="scope">
        <div style="display: flex; align-items: center">
          <el-icon><UserFilled /></el-icon>
          <span style="margin-left: 10px">{{ scope.row.username }}</span>
        </div>
      </template>
    </el-table-column>
  </el-table>
  <el-pagination background layout="slot, prev, pager, next" :total="total" @current-change="currentchange" @prev-click="prev" @next-click="next" v-model:current-page="currentpage">
    <template #default>
      <div style="color:#333;fontSize:13px">
        共{{total}}条
      </div>
    </template>
  </el-pagination>
</template>

<script>
import {getuserInfo} from '@/common/Interface.js'
export default {
  name:'dataman',
  created(){
    this.getlist()
  },
  data(){
    return{
      tableData:[],
      data:{
        limit:10,
        offset:0
      },
      currentpage:1,
      total:0
    }
  },
  methods:{
    async getlist(){
      const res = await getuserInfo(this.data)
      this.tableData = res[0]
      this.total = res[1][0].total
    },
    async prev() {
      this.data.offset = (this.currentpage-1)*10
      await this.getlist()
    },
    async next(){
      this.data.offset = (this.currentpage-1)*10
      await this.getlist()
    },
    async currentchange(){
      this.data.offset = (this.currentpage-1)*10
      await this.getlist()
    }
  }
}
</script>

<style lang="less" scoped>


</style>