<template>
    <div class="common-layout">
      <el-container>
        <el-header>
            <div class="title">{{!this.info.code?'用户设置':'商家设置'}}</div>
        </el-header>
        <el-main>
            <div class="contain">
                <div class="attribute">姓名:<span class="ml-2">{{!this.info.code?this.info.username:this.info.bname}}</span></div>
                <div class="attribute">注册时间:<span class="ml-2">{{this.info.date}}</span></div>
                <div class="attribute">权限:<span class="ml-2">{{!this.info.code?'用户':'商家'}}</span></div>
                <div class="attribute" v-if="this.info.code">商家码:<span class="ml-2">{{this.info.code}}</span></div>
                <div class="attribute">ID:<span class="ml-2">{{this.info.id}}</span></div>
                <div class="attribute">
                    <span>更新头像:</span>
                    <el-upload
                    class="avatar-uploader"
                    :show-file-list="false"
                    :http-request="beforeAvatarUpload"
                >
                    <img v-if="this.info.user_pic" :src="this.info.user_pic" class="avatar" />
                    <el-icon v-else class="avatar-uploader-icon"><Plus /></el-icon>
                    </el-upload>
                </div>
            </div>
        </el-main>
      </el-container>
    </div>
  </template>
  

<script>
import {changeavatar} from '@/common/Interface.js'
export default {
    name:'setting',
    mounted(){
        this.info = JSON.parse(localStorage.getItem('info'))
        window.addEventListener('setItem',(e) => {
            if(e.key === 'info')
            this.info = JSON.parse(e.newValue)
        })
    },
    data(){
        return{
            info:{},
            img:''
        }
    },
    methods:{
        beforeAvatarUpload(e){
            let file = e.file
            let index = file.name.lastIndexOf(".")
            let type = file.name.substr(index+1)
            const typeList = ["png","PNG","jpg","JPG"] 
            const size = file.size / 1024 / 1024 < 5
            if(!size){
                $messagee({
                    showClose:true,
                    message:'图片太大',
                    grouping:true,
                    type:'warning'
                })
                return false
            }else if (typeList.indexOf(type) < 0){
                $messagee({
                    showClose:true,
                    message:'图片格式不符',
                    type:'warning',
                    grouping:true
                })
                return false
            }else{
                const reader = new FileReader()
                reader.onload = async e => {
                    if(this.info.code){
                        const res = await changeavatar({pic:e.target.result,code:this.info.code})
                        let newinfo = {...this.info, user_pic:res}
                        $addStorage('info',JSON.stringify(newinfo))
                    }else{
                        const res = await changeavatar({pic:e.target.result})
                        let newinfo = {...this.info, user_pic:res}
                        $addStorage('info',JSON.stringify(newinfo))
                    }
                }
                reader.readAsDataURL(file)
            }
            },
        }
    }
</script>

<style lang="less" scoped>
    .title{
        position: absolute;
        top: 100px;
        left: 45%;
        font-size: 24px;
        color: #333;
        font-weight: 700;
    }
    .contain{
        position: relative;
        width: 60%;
        background-color: #f9fafc;
        min-height: 400px;
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        margin: 0 auto;
        padding-left: 20px;
        .attribute{
            font-size: 16px;
            color: #333;
        }
        .ml-2{
            margin-left: 5px;
        }
        .avatar-uploader .avatar {
            width: 130px;
            height: 130px;
            display: block;
        }
        /deep/.avatar-uploader .el-upload {
            border: 1px dashed #666;
            border-radius: 6px;
            cursor: pointer;
            position: relative;
            overflow: hidden;
            margin-top: 10px;
            background-color: #fff;
        }
        /deep/.avatar-uploader .el-upload:hover {
            border-color: cyan;
        }
        .el-icon.avatar-uploader-icon {
            font-size: 28px;
            color: #8c939d;
            width: 120px;
            height: 120px;
            text-align: center;
        }
    }

</style>