<template>
    <el-table :data="tableData" height="580" style="width: 100%" :header-cell-style="{ 
        background:'#eef1f6',color:'#000000',fontWeight:600}" :cell-style="{color:'#000'}">
        <el-table-column type="expand">
            <template #default="props">
                <div class="expand">
                  <div>
                    <p>食品ID: {{ props.row.id }}</p>
                    <p>食品名称: {{ props.row.foodname }}</p>
                    <p>食品特点: <span style="margin-left:5px" v-for="item in props.row.foodspecil">{{item}}</span></p>
                  </div>
                  <div>
                    <p>食品详情: {{ props.row.fooddetail }}</p>
                    <p>食品类型: {{ props.row.foodtype }}</p>
                  </div>
                </div>
            </template>
        </el-table-column>
      <el-table-column label="食品名称" prop="foodname" align="center"/>
      <el-table-column label="食品介绍" prop="fooddetail" align="center"/>
      <el-table-column label="评分" align="center">
        <template #default="scope">
          <el-rate
            v-model="scope.row.pingfen"
            disabled
            show-score
            text-color="#ff9900"
          />
        </template>
      </el-table-column>
      <el-table-column label="操作" align="center">
        <template #default="scope">
          <el-button size="small" @click="getIndex(scope)">编辑</el-button>
          <el-button size="small" type="danger" @click="dele">删除</el-button>
        </template>
    </el-table-column>
    </el-table>
    <el-pagination background layout="slot, prev, pager, next" :total="total" @current-change="currentchange" @prev-click="prev" @next-click="next" v-model:current-page="currentpage">
      <template #default>
        <div style="color:#333;fontSize:13px">
          共{{total}}条
        </div>
      </template>
    </el-pagination>
    <el-dialog v-model="dialog" title="修改食品信息" close-on-press-escape>
        <el-form class="dialog-form" label-width="80px" :rules="rule" ref="formRef" :model="fakeDate">
            <el-form-item required label="食品名称" prop="foodname">
                <el-input v-model="fakeDate.foodname" />
            </el-form-item>
            <el-form-item label="食品详情">
                <el-input v-model="fakeDate.fooddetail" />
            </el-form-item>
            <el-form-item label="食品分类" prop="foodtype">
              <el-select v-model="fakeDate.foodtype" :placeholder="fakeDate.foodtype">
                  <el-option
                  v-for="item in options"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value"
                  />
              </el-select>
            </el-form-item>
            <el-form-item label="食品图片">
                <el-upload
                class="avatar-uploader"
                :show-file-list="false"
                :http-request="beforeAvatarUpload"
                >
                <img v-if="fakeDate.foodimg" :src="fakeDate.foodimg" class="avatar" />
                <el-icon v-else class="avatar-uploader-icon"><Plus /></el-icon>
            </el-upload>
            </el-form-item>
            <el-form-item label-width="0">
                <el-table :data="fakeDate.guige" :header-cell-style="{ background:'#eef1f6',color:'#333'}" :cell-style="{color:'#333'}">
                    <el-table-column label="规格" prop="name"></el-table-column>
                    <el-table-column label="包装费" prop="moreprice"></el-table-column>
                    <el-table-column label="价格" prop="price"></el-table-column>
                    <el-table-column label="操作" >
                        <template #default="{$index}">
                            <el-button type="danger" icon="Delete" circle @click="del($index)"/>
                        </template>
                    </el-table-column>
                </el-table>
                <el-button style="margin:10px auto 0" type="primary" round @click="dialogtwo = true">添加规格</el-button>
            </el-form-item>
        </el-form>
        <template #footer>
            <el-button @click="dialog = false">取消</el-button>
            <el-button type="primary" @click="submit">确定</el-button>
        </template>
    </el-dialog>
    <el-dialog v-model="dialogtwo" title="添加规格" close-on-press-escape>
        <el-form class="dialog-form" label-width="80px" :model="dialogform">
            <el-form-item required label="规格">
                <el-input v-model="dialogform.name" />
            </el-form-item>
            <el-form-item label="包装费">
                <el-input-number v-model="dialogform.moreprice" :min="0" :max="30" controls-position="right"/>
            </el-form-item>
            <el-form-item label="价格">
                <el-input-number v-model="dialogform.price" :min="0" :max="30" controls-position="right"/>
            </el-form-item>
        </el-form>
        <template #footer>
            <el-button @click="dialogtwo = false">取消</el-button>
            <el-button type="primary" @click="addCheckArray">确定</el-button>
        </template>
    </el-dialog>
  </template>
  
  <script>
  import {getfoodInfo,updatefood} from '@/common/Interface.js'
  export default {
    name:'foodman',
    inject:['reload'],
    created(){
      this.getlist()
    },
    watch:{
        index:{
          handler(newvalue){
            this.fakeDate = {
              foodname:this.tableData[newvalue].foodname,
              foodtype:this.tableData[newvalue].foodtype,
              fooddetail:this.tableData[newvalue].fooddetail,
              foodimg:this.tableData[newvalue].foodimg,
              guige:this.tableData[newvalue].guige,
              id:this.tableData[newvalue].id
            }
          }
        }
    },
    data(){
      return{
        tableData:[],
        fakeDate:{},
        data:{
          limit:10,
          offset:0
        },
        dialogtwo:false,
        dialog:false,
        currentpage:1,
        total:0,
        index:null,
        options:[
            {
                value: '中餐',
                label: '中餐',
            },
            {
                value: '西餐',
                label: '西餐',
            },
            {
                value: '狗屎吃不吃',
                label: '狗屎吃不吃',
            },
        ],
        dialogform:{
            name:'默认',
            price:20,
            moreprice:1,
        },
        rule:{
            foodname: [
                { required: true, message: '请输入菜品名', trigger: 'blur' },
            ],
            foodtype:[
                { required: true, message: '请选择菜品类型', trigger: 'blur' },
            ],
        },
      }
    },
    methods:{
      async getlist(){
        const res = await getfoodInfo(this.data)
        this.total = res[1][0].total
        const arr = []
        res[0].forEach(e => {
          e.guige = JSON.parse(e.guige)
          e.foodspecil = JSON.parse(e.foodspecil)
          arr.push(e)
        });
        this.tableData = arr
      },
      async prev() {
        this.data.offset = (this.currentpage-1)*10
        await this.getlist()
  
      },
      async next(){
        this.data.offset = (this.currentpage-1)*10
        await this.getlist()
      },
      async currentchange(){
        this.data.offset = (this.currentpage-1)*10
        await this.getlist()
      },
      getIndex(e){
        this.dialog = true
        this.index = e.$index
      },
      addCheckArray(){
        if(this.fakeDate.guige == null){
          let arr = []
          arr.push(this.dialogform)
          this.fakeDate.guige = arr
          this.dialogtwo = false
        }else{
          this.fakeDate.guige.push(this.dialogform)
          this.dialogtwo = false
        }
      },
      del(i){
        this.fakeDate.guige.splice(i,1)
      },
      dele(){
        $message({
          showClose: true,
          message:'你不够资格',
          type:'info',
          grouping:true,
        })
      },
      beforeAvatarUpload(e){
        let file = e.file
        let index = file.name.lastIndexOf(".")
        let type = file.name.substr(index+1)
        const typeList = ["png","PNG","jpg","JPG"] 
        const size = file.size / 1024 / 1024 < 5
        if(!size){
            $messagee({
                showClose:true,
                message:'图片太大',
                grouping:true,
                type:'warning'
            })
            return false
        }else if (typeList.indexOf(type) < 0){
            $messagee({
                showClose:true,
                message:'图片格式不符',
                type:'warning',
                grouping:true
            })
            return false
        }else{
            const reader = new FileReader()
            reader.onload = async e => {
              this.fakeDate.foodimg = e.target.result
            }
            reader.readAsDataURL(file)
          }
      },
      async submit(){
        this.$refs.formRef.validate(async res => {
          if(!res) return
          await updatefood(this.fakeDate)
          this.dialog = false
          //this.getlist()
          this.reload()
        })
      }
    }
  }
  </script>
  
  <style lang="less" scoped>
      .expand{
        display:flex;
        justify-content:space-around;
        padding:0 20px;
      }
     .dialog-form{
            border: none;
            min-width: 0;
            margin-top: 0;
        }
      .avatar-uploader .avatar {
          width: 130px;
          height: 130px;
          display: block;
      }
      /deep/.avatar-uploader .el-upload {
          border: 1px dashed #666;
          border-radius: 6px;
          cursor: pointer;
          position: relative;
          overflow: hidden;
      }
      /deep/.avatar-uploader .el-upload:hover {
          border-color: cyan;
      }
      .el-icon.avatar-uploader-icon {
          font-size: 28px;
          color: #8c939d;
          width: 120px;
          height: 120px;
          text-align: center;
      }
  </style>