<template>
    <el-row>
        <el-col :span="15">
            <header style="text-align:center;font-size:18px">添加食品</header>
            <el-form
            ref="formRef"
            label-width="100px"
            :model="form"
            :rules="rule"
        >     
                <el-form-item prop="foodtype" label="食品类型" show-message required>
                    <el-select v-model="form.foodtype" placeholder="请选择">
                            <el-option
                            v-for="item in options"
                            :key="item.value"
                            :label="item.label"
                            :value="item.value"
                            />
                    </el-select>
                </el-form-item>
                <el-form-item prop="foodname" show-message required label="食品名称">
                    <el-input  v-model="form.foodname"/>
                </el-form-item>
                <el-form-item label="食品详情">
                    <el-input  v-model="form.fooddetail"/>
                </el-form-item>
                <el-form-item label="上传食品图片">
                    <el-upload
                    class="avatar-uploader"
                    :show-file-list="false"
                    :http-request="beforeAvatarUpload"
                >
                    <img v-if="this.form.pic" :src="this.form.pic" class="avatar" />
                    <el-icon v-else class="avatar-uploader-icon"><Plus /></el-icon>
                </el-upload>
                </el-form-item>
                <el-form-item label="食品特点">
                    <el-select v-model="form.foodspecil"  multiple tag-type="warning" placeholder="请选择">
                            <el-option
                            v-for="item in optionstwo"
                            :key="item.value"
                            :value="item.value"
                            />
                    </el-select>
                </el-form-item>
                <el-form-item label="食品规格">
                    <el-radio-group v-model="radio">
                        <el-radio label="1">单规格</el-radio>
                        <el-radio @click="push" label="2">多规格</el-radio>
                     </el-radio-group>
                </el-form-item>
                <el-form-item label="价格" v-if="radio == '1'">
                    <el-input-number v-model="baseform.price" :min="0" :max="30"/>
                </el-form-item>          
                <el-form-item label="包装费" v-if="radio == '1'">
                    <el-input-number v-model="baseform.moreprice" :min="0" :max="30"/>
                </el-form-item>
                <el-form-item label-width="0" v-if="radio == '2'">
                    <el-button style="margin:0 auto 10px" type="primary" round @click="dialog = true">添加规格</el-button>
                    <el-table :data="form.guige" :header-cell-style="{ background:'#eef1f6',color:'#333'}" :cell-style="{color:'#333'}">
                        <el-table-column label="规格" prop="name"></el-table-column>
                        <el-table-column label="包装费" prop="moreprice"></el-table-column>
                        <el-table-column label="价格" prop="price"></el-table-column>
                        <el-table-column label="操作" >
                            <template #default="{$index}">
                                <el-button type="danger" icon="Delete" circle @click="del($index)"/>
                            </template>
                        </el-table-column>
                    </el-table>
                </el-form-item>
                <el-dialog v-model="dialog" title="添加规格" close-on-press-escape>
                    <el-form class="dialog-form" label-width="80px" :model="dialogform">
                        <el-form-item required label="规格">
                            <el-input v-model="dialogform.name" />
                        </el-form-item>
                        <el-form-item label="包装费">
                            <el-input-number v-model="dialogform.moreprice" :min="0" :max="30" controls-position="right"/>
                        </el-form-item>
                        <el-form-item label="价格">
                            <el-input-number v-model="dialogform.price" :min="0" :max="30" controls-position="right"/>
                        </el-form-item>
                    </el-form>
                    <template #footer>
                        <el-button @click="dialog = false">取消</el-button>
                        <el-button type="primary" @click="addCheckArray">确定</el-button>
                    </template>
                </el-dialog>
            </el-form>
            <footer style="text-align:center">
                <el-button size="large" type="primary"  @click="submit">确认添加食品</el-button>
            </footer>
        </el-col>
    </el-row>
  
</template>

<script>
import { addgoods } from '@/common/Interface'
export default {
    name:'addGoods',
    mounted(){
    },
    data(){
        return{
            radio:'1',
            dialog:false,
            baseform:{
                name:'默认',
                price:20,
                moreprice:1,
            },
            dialogform:{
                name:'默认',
                price:20,
                moreprice:1,
            },
            options:[
                {
                    value: '中餐',
                    label: '中餐',
                },
                {
                    value: '西餐',
                    label: '西餐',
                },
                {
                    value: '狗屎吃不吃',
                    label: '狗屎吃不吃',
                },
            ],
            optionstwo:[
                {value:'经典'},{value:'新品'}
            ],
            rule:{
            foodname: [
                { required: true, message: '请输入菜品名', trigger: 'blur' },
            ],
            foodtype:[
                { required: true, message: '请选择菜品类型', trigger: 'blur' },
            ],
        },
            form:{
                foodtype:'',
                foodname:'',
                fooddetail:'',
                foodspecil:'',
                pic:'',
                guige:[]
            },
        }
    },
    methods:{
        addCheckArray(){
            let obj = {
                name:this.dialogform.name,
                price:this.dialogform.price,
                moreprice:this.dialogform.moreprice,
            }
            this.form.guige.push(obj)
            this.dialog = false
        },
        del(i){
            this.form.guige.splice(i,1)
        },
        push(){
            if(this.form.guige.length<1)
            this.form.guige.push(this.baseform)
        },
        submit(){
            this.$refs.formRef.validate(async res => {
                if(!res) return
                else{
                    if(this.form.guige.length < 1){
                    this.form.guige.push(this.baseform)
                }
                //console.log(this.form);
                await addgoods(this.form)
                this.form = []
                }
            })
        },
        beforeAvatarUpload(e){
            let file = e.file
            let index = file.name.lastIndexOf(".")
            let type = file.name.substr(index+1)
            const typeList = ["png","PNG","jpg","JPG"] 
            const size = file.size / 1024 / 1024 < 5
            if(!size){
                $messagee({
                    showClose:true,
                    message:'图片太大',
                    grouping:true,
                    type:'warning'
                })
                return false
            }else if (typeList.indexOf(type) < 0){
                $messagee({
                    showClose:true,
                    message:'图片格式不符',
                    type:'warning',
                    grouping:true
                })
                return false
            }else{
                const reader = new FileReader()
                reader.onload = async e => {
                    this.form.pic = e.target.result
                }
                reader.readAsDataURL(file)
            }
        },
    }
}
</script>

<style lang="less" scoped>

    .el-col{
        margin: 0 auto;
        .el-form {
        padding: 10px;
        border: 1px solid #eaeefb;
        min-width: 400px;
        margin-top:20px;
        /deep/.el-form-item{
            margin-bottom: 20px;
            .el-form-item__label{
                font-size: 14px;
                color: #333;
                }
            }
        }
        .dialog-form{
            border: none;
            min-width: 0;
            margin-top: 0;
        }
        .avatar-uploader .avatar {
            width: 130px;
            height: 130px;
            display: block;
        }
        /deep/.avatar-uploader .el-upload {
            border: 1px dashed #666;
            border-radius: 6px;
            cursor: pointer;
            position: relative;
            overflow: hidden;
        }
        /deep/.avatar-uploader .el-upload:hover {
            border-color: cyan;
        }
        .el-icon.avatar-uploader-icon {
            font-size: 28px;
            color: #8c939d;
            width: 120px;
            height: 120px;
            text-align: center;
        }
    }


</style>